body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

canvas {
  position: absolute;
  width: 100%;
  height: 100%;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.Sticky {
  z-index: 1;
}

a {
    text-decoration: none;
}

.right {
    width: 100%;
    display: inline-block;
    vertical-align: top;
    padding: 30px;
}

.right ul {
    padding-left: 45px;
    margin-top: 5px;
    /* font-size: 15px; */
}

.right .exp-item:last-of-type {
    padding-bottom: 25px;
}

.right h4 {
    position: relative;
    margin: 0px 0px 0px;
    text-transform: uppercase;
}

.right .exp-item {
    margin-top: 30px;
}

.right .exp-item .location {
    position: relative;
    float: right;
    font-weight: 700;
    color: rgb(87, 84, 84);
}

.right .exp-item .title {
    font-weight: 700;
    font-style: italic;
}



@media screen and (max-width: 650px) {
    .right {
        display: block;
        width: 90%;
        padding: 20px;

    }

    .right h4 {
        font-size: large;
    }

    .right .exp-item .location {
        float: none;
    }

}



/* Mobile */
@media screen and (max-width: 450px) {

    .right {
        min-height: 15em;
        padding: 15px;
    }

}
.comma-list {
    list-style: square outside none;

    font-size: 100%;
    margin: 30px 60px;
    width: 100%;
}
.active {
  color: #fffdff !important;
}

